import React, { Component } from 'react';
import SVG from 'react-inlinesvg';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
const MoveTo = React.lazy(() => import('../Actions/MoveTo'));
const BulkCopy = React.lazy(() => import('../Actions/BulkCopy'));

const SelectAllOptions = ({ myAssetsData, addTagModalShow, collections, postPostMove, onSelectToMove, onSelectToTags, onSelectToCopy, onSelectMoveToTrash, rightsHandleModalShow, onSelectPostStatus }) => {
    return <>{Object.keys(myAssetsData.selectedData).length > 0 ?
        <div className="d-flex align-items-center flex-wrap my-3">
            <div className="d-none d-lg-flex">
                <MoveTo collections={collections} onSelectToMove={onSelectToMove} />
                <button type="button" className="btn p-0 d-inline-flex align-items-center me-2" onClick={addTagModalShow(null)}>
                    <i className="ri-price-tag-3-line icon-6 me-1"></i>
                    <span className="text-gray-800 fs-7">Add Tag</span>
                </button>
                <button type="button" className="btn p-0 d-inline-flex align-items-center me-2" onClick={rightsHandleModalShow(null)}>
                    <span className="svg-icon svg-icon-5 text-gray-800">
                        <SVG src={baseUrl('media/icons/svg-icons/rights-line.svg')} height={16} width={16} />
                    </span>
                    <span className="text-gray-800 fs-7">Get Rights</span>
                </button>

                <BulkCopy collections={collections} postPostMove={postPostMove} onSelectToCopy={onSelectToCopy} />
                <button type="button" className="btn p-0 d-inline-flex align-items-center me-2" onClick={onSelectMoveToTrash}>
                    <i className="ri-delete-bin-6-line icon-6 me-1"></i>
                    <span className="text-gray-800 fs-7">Move to Trash</span>
                </button>
                <button type="button" className="btn p-0 d-inline-flex align-items-center me-2" onClick={onSelectPostStatus("1")}>
                    <i className="ri-eye-line icon-6 me-1"></i>
                    <span className="text-gray-800 fs-7">Show</span>
                </button>
                <button type="button" className="btn p-0 d-inline-flex align-items-center me-2" onClick={onSelectPostStatus("2")}>
                    <i className="ri-eye-off-line icon-6 me-1"></i>
                    <span className="text-gray-800 fs-7">Hide</span>
                </button>
            </div>
        </div>
        : null}</>
}
export default SelectAllOptions